package catalystpage.com.screens

import androidx.compose.runtime.*
import catalystpage.com.database.LocalUserState
import catalystpage.com.database.getLoggedInUser
import catalystpage.com.database.logOutUser
import catalystpage.com.database.signInWithGoogle
import catalystpage.com.util.Constants.FONT_FAMILY
import com.varabyte.kobweb.compose.css.FontStyle
import com.varabyte.kobweb.compose.css.FontWeight
import com.varabyte.kobweb.compose.foundation.layout.Arrangement
import com.varabyte.kobweb.compose.foundation.layout.Box
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.ui.Alignment
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.graphics.Colors
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.compose.ui.toAttrs
import com.varabyte.kobweb.silk.style.breakpoint.Breakpoint
import com.varabyte.kobweb.silk.theme.breakpoint.rememberBreakpoint
import org.jetbrains.compose.web.css.percent
import org.jetbrains.compose.web.css.px
import org.jetbrains.compose.web.dom.Button
import org.jetbrains.compose.web.dom.P
import org.jetbrains.compose.web.dom.Text


@Composable
fun AuthScreen() {
    val userState = LocalUserState.current
    var isVisible by remember { mutableStateOf(false) }
    var loggedInEmail by remember { mutableStateOf<String?>(null) }
    val breakpoint = rememberBreakpoint()

    LaunchedEffect(Unit) {
        isVisible = true
        loggedInEmail = getLoggedInUser()
    }

    Box(
        modifier = Modifier.fillMaxSize(),
        contentAlignment = Alignment.Center
    ) {
        Column (
            modifier = Modifier
                .fillMaxWidth(if (breakpoint <= Breakpoint.MD) 100.percent else 50.percent)
                .borderRadius(10.px),
            horizontalAlignment = Alignment.CenterHorizontally,
            verticalArrangement = Arrangement.Center
        ) {
            P(
                attrs = Modifier
                    .margin(bottom = 10.px)
                    .fontSize(30.px)
                    .fontFamily(FONT_FAMILY)
                    .fontWeight(FontWeight.Bold)
                    .fontStyle(FontStyle.Italic)
                    .color(Colors.DimGray)
                    .toAttrs()
            ) {
                Text("Welcome to Catalyst")
            }

            if (userState.value == null) {
                Button(
                    attrs = Modifier
                        .backgroundColor(Colors.LightBlue)
                        .borderRadius(5.px)
                        .fontFamily(FONT_FAMILY)
                        .align(Alignment.Start)
                        .padding(10.px)
                        .onClick {
                            signInWithGoogle()
                        }
                        .toAttrs()
                ) {
                    Text("Sign in with Google")
                }
            } else {
                Text(" Signed in as ${userState.value!!.email}")

                Button(
                    attrs = Modifier
                        .backgroundColor(Colors.Tomato)
                        .borderRadius(5.px)
                        .margin(top = 20.px)
                        .padding(10.px)
                        .toAttrs {
                            onClick {
                                logOutUser()
                                loggedInEmail = null
                            }
                        }
                ) {
                    Text("Logout")
                }
            }
        }
    }
}