package catalystpage.com.dashboards

import androidx.compose.runtime.*
import catalystpage.com.components.BundleCard
import catalystpage.com.components.ProductVariantCard
import catalystpage.com.database.JsBundleFetcher
import catalystpage.com.database.JsProductFetcher
import catalystpage.com.model.DashboardItem
import com.varabyte.kobweb.compose.foundation.layout.*
import com.varabyte.kobweb.compose.ui.Alignment
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.silk.components.layout.SimpleGrid
import com.varabyte.kobweb.silk.components.layout.numColumns
import com.varabyte.kobweb.silk.style.breakpoint.Breakpoint
import com.varabyte.kobweb.silk.theme.breakpoint.rememberBreakpoint
import dto.BundleDTO
import dto.ProductDTO
import org.jetbrains.compose.web.css.percent
import org.jetbrains.compose.web.css.px

@Composable
fun ShopSection() {
    val breakpoint = rememberBreakpoint()
    Box(
        modifier = Modifier
            .id(DashboardItem.Shop.id)
            .apply {
                if (breakpoint > Breakpoint.MD) {
                   0.px
                } else {
                    margin(left = 50.px, bottom = 20.px)
                }
            }
            .maxWidth(1450.px)
            .fillMaxWidth(
                if (breakpoint <= Breakpoint.MD) 75.percent else 80.percent
            ),
        contentAlignment = Alignment.TopStart
    ) {
        ShopContent()
    }
}


@Composable
fun ShopContent() {
    val breakpoint = rememberBreakpoint()
    var bundles by remember { mutableStateOf<List<BundleDTO>>(emptyList()) }
    var products by remember { mutableStateOf<List<ProductDTO>>(emptyList()) }

    //  Fetch the bundles from your backend API
    LaunchedEffect(Unit) {
        try {
            bundles = JsBundleFetcher().fetchBundles()
            products = JsProductFetcher().fetchProducts()
        } catch (e: Exception) {
            console.error(" Failed to load bundles: ${e.message}")
        }
    }

    Column(
        modifier = Modifier
            .fillMaxWidth(
                if (breakpoint <= Breakpoint.MD) 75.percent else 80.percent
            )
            .margin(bottom = 25.px)
    ) {
        SimpleGrid(
            numColumns = numColumns(base = 1, md = 2)
        ) {
            //  Loop through the fetched bundles and pass them to BundleCard
            bundles.forEach { bundle ->
                BundleCard(
                    bundle = bundle,
                    imageUrl = bundle.imageUrl?: "https://storage.googleapis.com/catalyst-cloud-storage/mix1.png",
                    title = bundle.name
                )
            }
            products.forEach { product ->
                ProductVariantCard(
                    product = product,
                    imageUrl = product.imageUrl?: "https://storage.googleapis.com/catalyst-cloud-storage/20.png",
                    title = product.name
                )
            }
        }
    }
}
