package catalystpage.com.database

import kotlinx.browser.window
import org.w3c.fetch.RequestInit
import org.w3c.fetch.Response
import org.w3c.xhr.FormData
import org.w3c.files.File

fun uploadPaymentProof(
    orderId: Int,
    file: File,
    onResult: (String) -> Unit
) {
    val formData = FormData().apply {
        append("order_id", orderId.toString())
        append("payment_method", "Gcash") // You can make this dynamic if needed
        append("file", file) //  attach the uploaded file here
    }

    window.fetch("/api/payments/upload", RequestInit(
        method = "POST",
        body = formData
    )).then { response ->
        val res = response as Response

        if (res.ok) {
            res.text().then { text ->
                onResult(" Upload successful!")
            }
        } else {
            onResult(" Upload failed: ${res.status}")
        }
    }.catch { err ->
        onResult(" Upload error: ${err.message}")
    }
}