package catalystpage.com.database

import dto.BundleDTO
import dto.CartItemDTO
import dto.CheckoutRequest
import dto.ProductDTO
import io.ktor.client.*
import io.ktor.client.call.*
import io.ktor.client.engine.js.*
import io.ktor.client.plugins.*
import io.ktor.client.plugins.contentnegotiation.*
import io.ktor.client.request.*
import io.ktor.client.statement.*
import io.ktor.http.*
import io.ktor.serialization.kotlinx.json.*
import kotlinx.browser.window
import kotlinx.coroutines.await
import kotlinx.serialization.json.Json
import org.w3c.fetch.RequestInit

object CheckoutService {


    private val client = HttpClient(Js) {
        install(ContentNegotiation) {
            json(Json {
                ignoreUnknownKeys = true
                isLenient = true
            })
        }

        defaultRequest {
            url {
                protocol = URLProtocol.HTTP
                host = "localhost"
                port = 8081
            }
        }
    }


    suspend fun checkout(
        firebaseUid: String,
        address: String,
        items: List<CartItemDTO>
    ): Int? {
        val request = CheckoutRequest(
            firebaseUid = firebaseUid,
            address = address,
            items = items,
            courier = null
        )

        return try {
            val response = client.post("/api/orders/checkout") {
                contentType(ContentType.Application.Json)
                setBody(request)
            }

            if (response.status.isSuccess()) {
                val json = JSON.parse<dynamic>(response.bodyAsText())
                val orderId = json.orderId as Int
                println(" Checkout success: Order ID = $orderId")
                orderId
            } else {
                println("Checkout failed: ${response.bodyAsText()}")
                null
            }
        } catch (e: Exception) {
            println("Exception during checkout: ${e.message}")
            null
        }
    }

    suspend fun getPendingOrder(firebaseUid: String): Int? {
        return try {
            val response = client.get("/api/orders/pending") {
                parameter("uid", firebaseUid)
            }

            if (response.status.isSuccess()) {
                val json = JSON.parse<dynamic>(response.bodyAsText())
                json.orderId as Int
            } else {
                null
            }
        } catch (e: Exception) {
            println("Failed to fetch pending order: ${e.message}")
            null
        }
    }

    suspend fun getBundleInfoByVariantId(variantId: Int): BundleDTO {
        return try {
            client.get("/api/bundle-variants/bundle-from-variant/$variantId").body()
        } catch (e: Exception) {
            println("Failed to fetch bundle by variant ID $variantId: ${e.message}")
            throw e
        }
    }


    suspend fun getCartItemsByOrderId(orderId: Int): List<CartItemDTO> {
        return try {
            val response = client.get("/api/orders/$orderId/items")

            if (response.status.isSuccess()) {
                response.body()
            } else {
                println(" Failed to fetch cart items for order $orderId: ${response.status}")
                emptyList()
            }
        } catch (e: Exception) {
            println(" Exception during cart fetch: ${e.message}")
            emptyList()
        }
    }
    suspend fun getProductInfoByVariantId(variantId: Int): ProductDTO {
        return try {
            client.get("/api/products-variant/$variantId").body()
        } catch (e: Exception) {
            println(" Failed to fetch product by variant ID $variantId: ${e.message}")
            throw e
        }
    }
    suspend fun deleteOrder(orderId: Int, onComplete: () -> Unit) {
        try {
            val response = client.delete("/api/orders/$orderId")
            if (response.status.isSuccess()) {
                println(" Order $orderId successfully deleted.")
                onComplete()
            } else {
                println(" Failed to delete order $orderId: ${response.status}")
            }
        } catch (e: Exception) {
            println(" Exception while deleting order $orderId: ${e.message}")
        }
    }

}