package catalystpage.com.database

import dto.BundleDTO
import dto.BundleVariantDTO
import kotlinx.browser.window
import kotlinx.coroutines.await
import kotlinx.serialization.json.Json

class JsBundleFetcher {
    companion object {
        private val json = Json { ignoreUnknownKeys = true }
    }

    suspend fun fetchBundles(): List<BundleDTO> {
        val response = window.fetch("http://localhost:8081/api/bundles").await()

        if (!response.ok) {
            val errorBody = response.text().await()
            console.error(" Failed to fetch bundles:", response.status, response.statusText, errorBody)
            throw Exception("Failed to fetch bundles: ${response.status} ${response.statusText}")
        }

        val text = response.text().await()

        if (text.isBlank()) {
            console.warn(" Bundle response body is empty")
            return emptyList()
        }

        return json.decodeFromString(text)
    }

    suspend fun fetchBundleVariants(bundleId: String): List<BundleVariantDTO> {
        val response = window.fetch("http://localhost:8081/api/bundles/$bundleId/variants").await()

        if (!response.ok) {
            val errorBody = response.text().await()
            console.error(" Failed to fetch bundle variants:", response.status, response.statusText, errorBody)
            throw Exception("Failed to fetch bundle variants: ${response.status} ${response.statusText}")
        }

        val text = response.text().await()

        if (text.isBlank()) {
            console.warn(" Bundle variants response body is empty")
            return emptyList()
        }

        return json.decodeFromString(text)
    }
}