@Composable
fun BundleCard(
    bundle: BundleDTO,
    imageUrl: String,
    title: String
) {
    var selectedPack by remember { mutableStateOf(12) } // Default to 12-pack
    val breakpoint = rememberBreakpoint()

    Column(
        modifier = Modifier
            .backgroundColor(Colors.DimGray)
            .width(100.percent)
            .boxShadow(
                blurRadius = 5.px,
                spreadRadius = 0.px,
                color = Colors.DarkGray,
                offsetX = 0.px,
                offsetY = 4.px
            )
            .maxWidth(500.px)
            .margin(
                bottom = if(breakpoint >= Breakpoint.MD) 100.px else 40.px,
                left =  if (breakpoint >= Breakpoint.MD)50.px else 35.px
            )
            .borderRadius(r = 20.px),
        horizontalAlignment = Alignment.Start,
        verticalArrangement = Arrangement.spacedBy(20.px)
    ) {
        Box(
            modifier = Modifier
                .width(100.percent)
                .maxWidth(500.px),
            contentAlignment = Alignment.TopStart
        ) {
            Image(
                src = imageUrl,
                modifier = Modifier
                    .borderRadius(topRight = 20.px, topLeft = 20.px)
                    .width(100.percent)
                    .maxWidth(500.px)
                    .objectFit(ObjectFit.Cover),
                alt = title
            )
            Row(
                modifier = Modifier
                    .margin(top = 10.px, left = 10.px),
                horizontalArrangement = Arrangement.Start
            ) {
                P(
                    attrs = Modifier
                        .fontFamily(FONT_FAMILY)
                        .fontStyle(FontStyle.Italic)
                        .fontSize(15.px)
                        .padding(topBottom = 2.px, leftRight = 10.px)
                        .borderRadius(r = 20.px)
                        .backgroundColor(Colors.GhostWhite)
                        .border(style = LineStyle.Dotted, color = Colors.Black)
                        .toAttrs()
                ) {
                    Text("New")
                }
            }
        }
        Column(
            modifier = Modifier
                .width(100.percent)
                .padding(10.px)
        ) {
            ProductLabelPack(selectedPack) { selectedPack = it }
            BundleDetails(selectedPack = selectedPack, bundle)



        }
    }
}

@Composable
fun ProductLabelPack(selectedPack: Int, onPackSelected: (Int) -> Unit) {
    var expanded by remember { mutableStateOf(false) }


    Column(modifier = Modifier.width(100.percent)) {
        Row(
            modifier = Modifier
                .width(100.percent)
                .maxWidth(500.px),
            horizontalArrangement = Arrangement.SpaceBetween
        ) {
            Row(
                modifier = Modifier
                    .width(100.percent)
                    .maxWidth(250.px),
                horizontalArrangement = Arrangement.Start,
                verticalAlignment = Alignment.CenterVertically
            ) {
                P(
                    attrs = Modifier
                        .fontSize(24.px)
                        .fontFamily(FONT_FAMILY)
                        .color(Colors.WhiteSmoke)
                        .fontWeight(FontWeight.Bold)
                        .toAttrs()
                ) {
                    Text("Variety Pack")
                }
            }

            Row(
                modifier = Modifier
                    .width(100.percent)
                    .maxWidth(250.px),
                horizontalArrangement = Arrangement.End,
                verticalAlignment = Alignment.CenterVertically
            ) {
                Div(attrs = Modifier.classNames("dropdown").toAttrs()) {
                    Button(
                        attrs = Modifier
                            .fontFamily(FONT_FAMILY)
                            .classNames("btn", "btn-secondary", "dropdown-toggle")
                            .onClick { expanded = !expanded }
                            .toAttrs()
                    ) {
                        Text("$selectedPack Bottles")
                    }

                    if (expanded) {
                        Div(
                            attrs = Modifier.classNames("dropdown-menu", "show", "dropdown-menu-end")
                                .styleModifier {
                                    fontFamily(FONT_FAMILY)
                                    property("max-width", "150px")
                                    property("min-width", "120px")
                                }
                                .toAttrs()
                        ) {
                            listOf(24, 12, 8, 4).forEach { count ->
                                A(
                                    attrs = Modifier
                                        .classNames("dropdown-item")
                                        .onClick {
                                            onPackSelected(count)
                                            expanded = false
                                        }
                                        .toAttrs()
                                ) {
                                    Text("$count Bottles")
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

@Composable
fun BundleDetails(selectedPack: Int, bundle: BundleDTO) {
    val scope = rememberCoroutineScope()
    val isMobile = rememberBreakpoint() <= Breakpoint.MD

    var isSubscription by remember { mutableStateOf(false) }
    var variants by remember { mutableStateOf<List<BundleVariantDTO>>(emptyList()) }
    var selectedVariant by remember { mutableStateOf<BundleVariantDTO?>(null) }

    val discountMap = mapOf(4 to 0.05, 12 to 0.08, 24 to 0.10)
    val discount = discountMap[selectedPack] ?: 0.0

    val basePrice = selectedVariant?.price ?: 0.0
    val discountedPrice = (basePrice * (1 - discount)).toInt()


    LaunchedEffect(bundle.id) {
        try {
            variants = JsBundleFetcher().fetchBundleVariants(bundle.id.toString())
        } catch (e: Exception) {
            println(" Error loading variants: ${e.message}")
        }
    }
    LaunchedEffect(selectedPack, variants) {
        selectedVariant = variants.firstOrNull { it.quantity == selectedPack }
    }

    //  Add to Cart function
    val addToCart = fun() {
        val firebaseUid = window.localStorage.getItem("firebaseUserUID")
        val variantId = selectedVariant?.id

        if (firebaseUid == null || variantId == null) {
            println(" Missing user ID or variant ID")
            return
        }

        val item = CartItemDTO(
            id = null,
            firebaseUid = firebaseUid,
            quantity = 1,
            productVariantId = null,
            bundleVariantId = variantId
        )

        scope.launch {
            try {
                val response = JsCartFetcher().addToCart(item)
                println(" Added to cart: $response")
            } catch (e: Exception) {
                println(" Failed to add to cart: ${e.message}")
            }
        }
    }

    // UI
    if (selectedVariant != null) {
        Column {
            P(attrs = Modifier.fontSize(16.px).fontWeight(FontWeight.Bold).fontFamily(FONT_FAMILY).color(Colors.White).toAttrs())
            {
                Text(bundle.name)
            }
            P(attrs = Modifier.fontFamily(FONT_FAMILY).color(Colors.White).toAttrs())
            {
                Text(bundle.description ?: "")
            }
            P(attrs = Modifier.fontFamily(FONT_FAMILY).color(Colors.White).toAttrs())
            {
                Text("Size: ${selectedVariant!!.size}")
            }
            P(attrs = Modifier.fontFamily(FONT_FAMILY).color(Colors.White).toAttrs())
            {
                Text("Price: PHP ${if (isSubscription) discountedPrice else basePrice.toInt()}")
            }

            Button(
                attrs = Modifier
                    .boxShadow(
                        blurRadius = 2.px,
                        spreadRadius = 0.px,
                        color = Colors.Grey,
                        offsetX = 0.px,
                        offsetY = 1.px
                    )
                    .color(Colors.White)
                    .backgroundColor(Colors.Grey)
                    .fontWeight(FontWeight.Medium)
                    .fontSize(8.px)
                    .border(style = LineStyle.None)
                    .padding(5.px)
                    .fontWeight(FontWeight.Medium)
                    .fontFamily(FONT_FAMILY)
                    .borderRadius(r = 5.px)
                    .onClick {
                        addToCart() //  Trigger add to cart on click
                }.toAttrs()
            ) {
                Text("ADD TO CART")
            }
        }
    } else {
        Text("Loading variant...")
    }
}



