package catalystpage.com.database

import catalystpage.com.firebase.*
import dto.Role
import dto.UserDTO
import kotlinx.browser.window
import kotlinx.serialization.json.Json
import org.w3c.fetch.RequestInit

val auth = getAuth(FirebaseConfig.firebaseApp)

val jsonParser = Json {
    ignoreUnknownKeys = true
    isLenient = true
}

fun signInWithGoogle() {
    val provider = GoogleAuthProvider()

    signInWithPopup(auth, provider)
        .then { result ->
            val user = result.user
            if (user != null) {
                val userData = UserDTO(
                    firebaseUid = (user.uid ?: "") as String,
                    email = user.email as? String,
                    name = user.displayName as? String,
                    phone = user.phoneNumber as? String,
                    role = if (user.email == "maryroseaquino15@gmail.com") Role.ADMIN else null
                )
                val jsonBody = Json.encodeToString(userData)

                window.localStorage.setItem("firebaseUserUID", user.uid as String)
                window.localStorage.setItem("firebaseUserEmail", (user.email ?: "") as String)

                window.fetch("http://localhost:8081/api/auth/sync", RequestInit(
                    method = "POST",
                    headers = js("{ 'Content-Type': 'application/json' }"),
                    body = jsonBody
                )).then { response ->
                    response.text().then { rawText ->
                        console.log("Received response body:", rawText)
                        val userDTO = jsonParser.decodeFromString<UserDTO>(rawText)
                        onUserSynced(userDTO)
                    }
                }.catch {
                    console.error(" Failed to sync user:", it)
                }
            }
        }
        .catch { error ->
            console.error(" Sign-in failed:", error)
        }
}



fun getLoggedInUser(): String? {
    val user = auth.currentUser

    return if (user != null) {
        console.log("User is already logged in: ${user.email}")
        user.email as? String
    } else {
        console.log("No user logged in")
        null
    }
}


fun logOutUser() {
    signOut(auth)
        .then {
            window.localStorage.removeItem("firebaseUserUID")
            window.localStorage.removeItem("firebaseUserEmail")
            console.log("User logged out successfully")
        }
        .catch { error ->
            console.error("Logout failed:", error)
        }
}

fun onUserSynced(user: UserDTO) {
    window.alert(" Welcome ${user.name ?: user.email}! Your account has been synced.")

    if (user.role == Role.ADMIN) {
        console.log(" Admin signed in")
        window.location.href = "/dashboard"
    } else {
        window.location.href = "/dashboard"
    }
}