package catalystpage.com.dashboards

import androidx.compose.runtime.*
import catalystpage.com.database.CheckoutService.deleteOrder
import catalystpage.com.database.CheckoutService.getBundleInfoByVariantId
import catalystpage.com.database.CheckoutService.getCartItemsByOrderId
import catalystpage.com.database.CheckoutService.getProductInfoByVariantId
import catalystpage.com.database.uploadPaymentProof
import catalystpage.com.styles.ButtonStyles
import catalystpage.com.util.Constants.FONT_FAMILY
import com.varabyte.kobweb.compose.css.Cursor
import com.varabyte.kobweb.compose.css.FontWeight
import com.varabyte.kobweb.compose.foundation.layout.Arrangement
import com.varabyte.kobweb.compose.foundation.layout.Box
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.foundation.layout.Row
import com.varabyte.kobweb.compose.ui.Alignment
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.graphics.Colors
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.compose.ui.toAttrs
import com.varabyte.kobweb.silk.components.graphics.Image
import com.varabyte.kobweb.silk.style.toModifier
import dto.CartItemDTO
import kotlinx.coroutines.launch
import org.jetbrains.compose.web.attributes.InputType
import org.jetbrains.compose.web.css.px
import org.jetbrains.compose.web.dom.*
import org.w3c.dom.HTMLInputElement
import org.w3c.files.File


@Composable
fun CheckoutPage(
    orderId: Int,
    firebaseUid: String,
    address: String,
    onBackToCart: () -> Unit
) {
    var enrichedCartItems by remember { mutableStateOf<List<CartItemDTO>>(emptyList()) }
    var selectedFile by remember { mutableStateOf<File?>(null) }
    var uploadSuccess by remember { mutableStateOf(false) }
    var message by remember { mutableStateOf<String?>(null) }

    val coroutineScope = rememberCoroutineScope()

    LaunchedEffect(orderId) {
        val rawCartItems = getCartItemsByOrderId(orderId)

        enrichedCartItems = rawCartItems.map { item ->
            when {
                item.bundleVariantId != null -> {
                    val bundle = getBundleInfoByVariantId(item.bundleVariantId)
                    val variant = bundle.variants.find { it.id == item.bundleVariantId }

                    item.copy(
                        bundleName = bundle.name,
                        price = variant?.price ?: 0.0,
                        packSize = variant?.packSize
                    )
                }

                item.productVariantId != null -> {
                    val product = getProductInfoByVariantId(item.productVariantId)
                    val variant = product.variants.find { it.id == item.productVariantId }

                    item.copy(
                        productName = product.name,
                        price = variant?.price ?: 0.0,
                        packSize = variant?.packSize
                    )
                }

                else -> item
            }
        }
    }

    if (enrichedCartItems.isEmpty()) {
        Text("Loading checkout data...")
        return
    }

    val total = enrichedCartItems.sumOf { (it.price ?: 0.0) * it.quantity }
    val formattedTotal = total.asDynamic().toFixed(2) as String

    Box(
        modifier = Modifier
            .fillMaxWidth()
            .borderRadius(r = 15.px)
            .backgroundColor(Colors.White)
            .maxWidth(1600.px),
        contentAlignment = Alignment.TopStart
    ) {
        Column(modifier = Modifier.fillMaxSize().padding(16.px)) {

            P(attrs = Modifier
                .fontFamily(FONT_FAMILY)
                .fillMaxWidth()
                .borderRadius(topRight = 15.px, topLeft = 15.px)
                .padding(10.px)
                .fontSize(18.px).fontWeight(FontWeight.Bold)
                .backgroundColor(Colors.PaleTurquoise)
                .margin(bottom = 12.px)
                .toAttrs()
            ) {
                Text("Checkout Summary")
            }

            Row(
                modifier = Modifier.fillMaxWidth(),
                verticalAlignment = Alignment.CenterVertically,
                horizontalArrangement = Arrangement.SpaceBetween
            ) {
                Button(
                    attrs = ButtonStyles.toModifier()
                        .margin(bottom = 10.px)
                        .borderRadius(r = 15.px)
                        .fontFamily(FONT_FAMILY)
                        .onClick { onBackToCart() }
                        .toAttrs()
                ) {
                    Text("Back to Cart")
                }

                Button(
                    attrs = ButtonStyles.toModifier()
                        .margin(bottom = 10.px)
                        .borderRadius(r = 15.px)
                        .fontFamily(FONT_FAMILY)
                        .onClick {
                            coroutineScope.launch {
                                deleteOrder(orderId) {
                                    println("Order deleted, returning to cart")
                                    onBackToCart()
                                }
                            }
                        }.toAttrs()
                ) {
                    Text("Cancel Order")
                }
            }

            P(attrs = Modifier.fontFamily(FONT_FAMILY).margin(bottom = 10.px).toAttrs()) {
                Text("Order #00$orderId")
            }

            // Bootstrap Table
            Div(
                attrs = Modifier.classNames(
                    "relative", "overflow-x-auto", "mb-8", "shadow-md", "sm:rounded-lg"
                ).toAttrs()
            ) {
                Table(
                    attrs = Modifier.classNames(
                        "w-full", "text-sm", "text-left", "rtl:text-right",
                        "text-gray-500", "dark:text-gray-400"
                    ).toAttrs()
                ) {
                    Thead(
                        attrs = Modifier.classNames(
                            "text-xs", "text-gray-700", "uppercase", "bg-gray-50",
                            "dark:bg-gray-700", "dark:text-gray-400"
                        ).toAttrs()
                    ) {
                        Tr {
                            listOf("Qty", "Item", "Pack", "Unit", "Subtotal").forEach { label ->
                                Th(
                                    attrs = Modifier.classNames("px-6", "py-3").toAttrs {
                                        attr("scope", "col")
                                    }
                                ) {
                                    Text(label)
                                }
                            }
                        }
                    }

                    Tbody {
                        enrichedCartItems.forEach { item ->
                            val name = item.bundleName ?: item.productName ?: "Unknown Item"
                            val unitPrice = item.price ?: 0.0
                            val subtotal = unitPrice * item.quantity
                            val formattedUnit = unitPrice.asDynamic().toFixed(2) as String
                            val formattedSubtotal = subtotal.asDynamic().toFixed(2) as String
                            val pack = item.packSize?.toString() ?: "-"

                            Tr(
                                attrs = Modifier.classNames(
                                    "bg-white", "border-b",
                                    "dark:bg-gray-800", "dark:border-gray-700"
                                ).toAttrs()
                            ) {
                                Td(attrs = Modifier.classNames("px-6", "py-4").toAttrs()) {
                                    Text(item.quantity.toString())
                                }
                                Td(
                                    attrs = Modifier.classNames(
                                        "px-6", "py-4", "font-medium",
                                        "text-gray-900", "whitespace-nowrap", "dark:text-white"
                                    ).toAttrs()
                                ) {
                                    Text(name)
                                }
                                Td(attrs = Modifier.classNames("px-6", "py-4").toAttrs()) {
                                    Text(pack)
                                }
                                Td(attrs = Modifier.classNames("px-6", "py-4").toAttrs()) {
                                    Text("$formattedUnit")
                                }
                                Td(attrs = Modifier.classNames("px-6", "py-4").toAttrs()) {
                                    Text("$formattedSubtotal")
                                }
                            }
                        }
                    }
                }
            }

            // Shipping address
            P(attrs = Modifier.fontFamily(FONT_FAMILY).margin(top = 12.px, bottom = 12.px).toAttrs()) {
                Text("Shipping to: $address")
            }

            // GCash QR
            Text("Scan to Pay:")
            Image(
                src = "https://storage.googleapis.com/catalyst-cloud-storage/gcash-crop.jpg",
                modifier = Modifier.size(150.px)
            )

            // File input
            Input(type = InputType.File, attrs = Modifier.cursor(Cursor.Pointer).fontFamily(FONT_FAMILY).toAttrs {
                onInput { event ->
                    val fileInput = event.target as? HTMLInputElement
                    val file = fileInput?.files?.item(0)
                    selectedFile = file
                }
            })

            // Upload button
            Button(
                attrs = ButtonStyles.toModifier()
                    .margin(top = 10.px, bottom = 10.px)
                    .maxWidth(150.px)
                    .borderRadius(r = 15.px)
                    .cursor(Cursor.Pointer)
                    .fontFamily(FONT_FAMILY)
                    .onClick {
                        if (selectedFile != null) {
                            uploadPaymentProof(orderId, selectedFile!!) {
                                uploadSuccess = true
                                message = it
                            }
                        } else {
                            message = "Please select a file first."
                        }
                    }.toAttrs()
            ) {
                Text("Upload Payment Proof")
            }

            // Upload status
            if (uploadSuccess) {
                P(attrs = Modifier.color(Colors.Green).margin(top = 8.px).toAttrs()) {
                    Text("Payment uploaded! Awaiting admin approval...")
                }
            }

            message?.let {
                P(attrs = Modifier.color(Colors.Orange).margin(top = 8.px).toAttrs()) {
                    Text(it)
                }
            }
        }
    }
}