package catalystpage.com.sections

import androidx.compose.runtime.*
import catalystpage.com.components.Header
import catalystpage.com.model.Section
import catalystpage.com.screens.AuthScreen
import catalystpage.com.util.Constants.FONT_FAMILY
import catalystpage.com.util.Res
import com.varabyte.kobweb.compose.css.FontStyle
import com.varabyte.kobweb.compose.css.FontWeight
import com.varabyte.kobweb.compose.foundation.layout.Arrangement
import com.varabyte.kobweb.compose.foundation.layout.Box
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.foundation.layout.Row
import com.varabyte.kobweb.compose.ui.Alignment
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.graphics.Colors
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.compose.ui.toAttrs
import com.varabyte.kobweb.silk.components.graphics.Image
import com.varabyte.kobweb.silk.style.breakpoint.Breakpoint
import com.varabyte.kobweb.silk.theme.breakpoint.rememberBreakpoint
import org.jetbrains.compose.web.css.*
import org.jetbrains.compose.web.dom.P
import org.jetbrains.compose.web.dom.Span
import org.jetbrains.compose.web.dom.Text

@Composable
fun AuthSection(onMenuClicked: () -> Unit) {
    Box(
        modifier = Modifier
            .id(Section.SignIn.id)
            .fillMaxSize()
            .backgroundColor(Colors.DimGray),
        contentAlignment = Alignment.TopCenter
    ) {
        AuthContent(onMenuClicked = onMenuClicked)
    }
}

@Composable
fun AuthContent(onMenuClicked: () -> Unit) {
    val breakpoint = rememberBreakpoint()
    Column(
        modifier = Modifier.fillMaxSize(),
        verticalArrangement = Arrangement.Top,
        horizontalAlignment = Alignment.CenterHorizontally
    ) {
        Header(onMenuClicked = onMenuClicked)
        Row(
            modifier = Modifier
                .borderRadius(r = 10.px)
                .padding(topBottom = 20.px)
                .fillMaxWidth(
                    if (breakpoint > Breakpoint.MD)60.percent else 90.percent)
                .backgroundColor(Colors.White),
            verticalAlignment = Alignment.CenterVertically,
            horizontalArrangement = Arrangement.Center
        ) {
            AuthScreen()
            AuthArt(breakpoint = breakpoint)

        }
    }
}


@Composable
fun AuthArt(breakpoint: Breakpoint) {

    var isVisible by remember { mutableStateOf(false) }
    var translateX by remember { mutableStateOf(20.px) }
    var translateY by remember { mutableStateOf(20.px) }
    LaunchedEffect(Unit) {
        isVisible = true
        translateX = 0.px
        translateY = 0.px
    }

    Box(
        modifier = Modifier,
        contentAlignment = Alignment.Center
    ) {
        Image(
            modifier = Modifier
                .position(Position.Relative)
                .maxSize(500.px)
                .margin(right = 145.px)
                .then(if (breakpoint <= Breakpoint.MD) Modifier.display(DisplayStyle.None) else Modifier)
                .boxShadow(
                    offsetX = 1.px,
                    offsetY = 1.px,
                    blurRadius = 3.px,
                    spreadRadius = 1.px,
                    color = Colors.DimGray.copyf(alpha = 0.5f)

                )
                .borderRadius(r = 10.px),
            src = Res.Image.auth1,
            alt = "Image Background"
        )
        Box(
            modifier = Modifier
                .then(
                    if (breakpoint <= Breakpoint.MD) Modifier.display(DisplayStyle.None) //  Hide on small screens
                    else Modifier
                )
                .align(Alignment.BottomEnd) // Place it on top of the image
                .top(50.percent) // Adjust vertical position
                .left(50.percent) // Center horizontally
                .translateX(translateX)
                .translateY(translateY)
                .backgroundColor(Colors.PaleTurquoise)
                .opacity(80.percent)
                .padding(10.px)
                .border(style = LineStyle.None)
                .borderRadius(r = 10.px)
                .margin(25.px)
                .boxShadow(
                    offsetX = 0.px,
                    offsetY = 1.px,
                    blurRadius = 3.px,
                    spreadRadius = 1.px,
                    color = Colors.DimGray.copyf(alpha = 0.2f)

                )

        ) {
            Column {
                P(
                    attrs = Modifier
                        .fontFamily(FONT_FAMILY)
                        .fontStyle(FontStyle.Italic)
                        .fontWeight(FontWeight.Bold)
                        .color(Colors.Black)
                        .fontSize(15.px)
                        .margin(bottom = ((-5).px))
                        .toAttrs()
                ) {
                    Text("The process itself-")
                }
                P(
                    attrs = Modifier
                        .fontFamily(FONT_FAMILY)
                        .fontWeight(FontWeight.Thin)
                        .color(Colors.Black)
                        .fontSize(10.px)
                        .toAttrs()
                ) {
                    Span(
                        attrs = Modifier
                            .display(DisplayStyle.Block)
                            .margin(bottom = ((-10).px))
                            .toAttrs()
                    ) { Text("is where meaning is found.") }

                }
            }
        }
    }
}