@Composable
fun Greetings() {
    var isLoading by remember { mutableStateOf(true) }
    var userDto by remember { mutableStateOf<UserDTO?>(null) }
    val userFetcher = remember { JsUserFetcher() }

    val greeting = remember {
        val hour = js("new Date().getHours()") as Int
        when (hour) {
            in 5..11 -> "Good Morning,"
            in 12..17 -> "Good Afternoon,"
            in 18..21 -> "Good Evening,"
            else -> "Good Night,"
        }
    }

    val breakpoint = rememberBreakpoint()

    LaunchedEffect(Unit) {
        try {
            userDto = userFetcher.fetchCurrentUser()
        } catch (e: Throwable) {
            console.error(" Error fetching user:", e)
        } finally {
            isLoading = false
        }
    }

    Column(
        modifier = Modifier
            .borderRadius(r = 15.px)
            .fillMaxWidth()
            .padding(10.px)
            .boxShadow(
            blurRadius = 10.px,
            spreadRadius = 0.px,
            color = Colors.LightGray,
            offsetX = 0.px,
            offsetY = 4.px)
            .margin(bottom = 10.px)
            .backgroundColor(Colors.White),
        verticalArrangement = Arrangement.Center,
        horizontalAlignment = Alignment.Start
    ) {
        if (isLoading) {
            Text("Loading...")
        } else {
            P(
                attrs = Modifier
                    .fontFamily(FONT_FAMILY)
                    .fontWeight(FontWeight.Bold)
                    .fontStyle(FontStyle.Italic)
                    .color(Colors.DimGray)
                    .fontSize(if (breakpoint <= Breakpoint.MD) 15.px else 20.px)
                    .toAttrs()
            ) {
                Text(greeting)
            }
            P(
                attrs = Modifier
                    .fontFamily(FONT_FAMILY)
                    .fontWeight(FontWeight.Normal)
                    .color(Colors.DimGray)
                    .fontSize(if (breakpoint <= Breakpoint.MD) 15.px else 20.px)
                    .margin(left = 5.px, bottom = 15.px)
                    .toAttrs()
            ) {
                Text(userDto?.name ?: "Guest")
            }
        }
    }
}



