@Composable
fun AccountSettings() {
    val scope = rememberCoroutineScope()
    var userData by remember { mutableStateOf<UserDTO?>(null) }
    var isEditing by remember { mutableStateOf(false) }
    val breakpoint = rememberBreakpoint()

    // Fetch user data once
    LaunchedEffect(Unit) {
        scope.launch {
            userData = JsUserFetcher().fetchCurrentUser()
        }
    }

    Column(
        modifier = Modifier
            .boxShadow(
                blurRadius = 10.px,
                spreadRadius = 0.px,
                color = Colors.LightGray,
                offsetY = 4.px,
                offsetX = 0.px
            )
            .fillMaxWidth()
            .margin(topBottom = 10.px)
            .borderRadius(10.px)
            .padding(16.px)
            .backgroundImage(
                radialGradient(
                    shape = RadialGradient.Shape.Ellipse,
                    position = CSSPosition.BottomRight,
                    from = Colors.PaleTurquoise,
                    to = Colors.White
                )
            ),
        horizontalAlignment = Alignment.Start,
        verticalArrangement = Arrangement.spacedBy(12.px)
    ) {
        Row(
            modifier = Modifier.fillMaxWidth(),
            verticalAlignment = Alignment.CenterVertically,
            horizontalArrangement = Arrangement.SpaceBetween
        ) {
            P(
                attrs = Modifier
                    .fontSize(20.px)
                    .fontWeight(FontWeight.Bold)
                    .fontFamily(FONT_FAMILY)
                    .fontStyle(FontStyle.Italic)
                    .toAttrs()
            ) { Text("Your Profile") }

            if (!isEditing) {
                P(
                    attrs = Modifier
                        .fontFamily(FONT_FAMILY)
                        .color(Colors.Black)
                        .borderRadius(r = 50.px)
                        .cursor(Cursor.Pointer)
                        .padding(leftRight = 5.px, topBottom = 11.px)
                        .border(style = LineStyle.None)
                        .onClick { isEditing = true }
                        .toAttrs()
                ) {
                    Text("Edit")
                }
            }
        }

        if (!isEditing) {
            userData?.let { user ->
                ProfileRow("Full Name:", user.name ?: "Not Set")
                user.email?.let { ProfileRow("Email:", it) }
                ProfileRow("Phone:", user.phone ?: "Not Provided")
            } ?: Text("Loading...")
        } else {
            var fullName by remember { mutableStateOf(userData?.name ?: "") }
            var phone by remember { mutableStateOf(userData?.phone ?: "") }

            Column(
                modifier = Modifier.fillMaxWidth(),
                verticalArrangement = Arrangement.spacedBy(8.px)
            ) {
                Input(
                    InputType.Text, attrs = Modifier
                    .fontFamily(FONT_FAMILY)
                    .toAttrs {
                        value(fullName)
                        placeholder("Enter full name")
                        onInput { fullName = (it.target as HTMLInputElement).value }
                    })

                Input(InputType.Tel, attrs = Modifier
                    .fontFamily(FONT_FAMILY)
                    .toAttrs {
                        value(phone)
                        placeholder("Enter phone number")
                        onInput { phone = (it.target as HTMLInputElement).value }
                    })

                Button(
                    attrs = Modifier
                        .borderRadius(10.px)
                        .fontFamily(FONT_FAMILY)
                        .border(
                            style = LineStyle.Solid,
                            color = Colors.PaleTurquoise
                        )
                        .backgroundColor(Colors.White)
                        .toAttrs {
                            onClick {
                                console.log(" Save button clicked")
                                scope.launch {
                                    try {
                                        console.log(" Starting updateUserDTO...")
                                        val fetcher = JsUserFetcher()
                                        fetcher.updateUserDTO(fullName, phone)
                                        console.log(" Fetching updated user...")
                                        userData = fetcher.fetchCurrentUser()
                                        isEditing = false
                                    } catch (e: Exception) {
                                        console.error(" Error during update flow:", e.message)
                                    }
                                }
                            }
                        }
                ) {
                    Text("Save")
                }
            }
        }
    }
}

@Composable
fun ProfileRow(label: String, value: String) {
    Row {
        P(
            attrs = Modifier
                .width(100.px)
                .fontFamily(FONT_FAMILY)
                .fontWeight(FontWeight.Bold)
                .toAttrs()
        ) { Text(label) }

        P(
            attrs = Modifier
                .fontFamily(FONT_FAMILY)
                .toAttrs()
        ) { Text(value) }
    }
}