@Composable
fun CartSection(
    firebaseUid: String,
    onCheckoutSuccess: (Int) -> Unit
) {
    val breakpoint = rememberBreakpoint()
    Box(
        modifier = Modifier
            .margin(
                all = if (breakpoint <= Breakpoint.SM) 0.px else 100.px
            )
            .fillMaxWidth(
                if (breakpoint <= Breakpoint.SM) 50.percent else 100.percent
            )
            .maxWidth(1500.px),
        contentAlignment = Alignment.TopStart
    ) {
        CartPage(firebaseUid, onCheckoutSuccess = onCheckoutSuccess)
    }
}


@Composable
fun CartPage(
    firebaseUid: String,
    onCheckoutSuccess: (Int) -> Unit
) {
    val viewModel = remember { CartViewModel() }

    var currentPage by remember { mutableStateOf(0) }
    val itemsPerPage = 5
    val breakpoint = rememberBreakpoint()

    val cartItems = viewModel.cartItems
    var address by remember { mutableStateOf("") }
    var showCheckoutPage by remember { mutableStateOf(false) }
    var currentOrderId by remember { mutableStateOf<Int?>(null) }

    LaunchedEffect(firebaseUid) {
        val pendingOrderId = CheckoutService.getPendingOrder(firebaseUid)
        if (pendingOrderId != null) {
            currentOrderId = pendingOrderId
            showCheckoutPage = true
        } else {
            viewModel.loadCartItems(firebaseUid)
        }
    }

    if (showCheckoutPage && currentOrderId != null) {
        CheckoutPage(
            orderId = currentOrderId!!,
            firebaseUid = firebaseUid,
            address = address,
            onBackToCart = { showCheckoutPage = false }
        )
        return
    }

    val mergedItems = cartItems
        .groupBy { Pair(it.title, it.packSize) }
        .map { (_, group) ->
            val first = group.first()
            first.copy(quantity = group.sumOf { it.quantity })
        }

    val totalPages = (mergedItems.size + itemsPerPage - 1) / itemsPerPage
    val itemsForPage = mergedItems.drop(currentPage * itemsPerPage).take(itemsPerPage)
    val totalPrice = mergedItems.sumOf { it.packPrice.toDouble() * it.quantity }

    Column(modifier = Modifier.padding(20.px)) {
        CartHeader()

        if (breakpoint > Breakpoint.SM) {
            Row(
                Modifier.fillMaxWidth().padding(topBottom = 8.px)
            ) {
                HeaderCart.entries.forEach {
                    Box(
                        Modifier.margin(left = 10.px)
                            .fontFamily(FONT_FAMILY)
                            .fontWeight(FontWeight.Bold)
                            .width(300.px)
                            .padding(8.px)
                    ) { Text(it.header) }
                }
            }
        }

        itemsForPage.forEach { item ->
            CartCard(item = item, viewModel = viewModel)
        }

        PaginationControls(
            currentPage = currentPage,
            totalPages = totalPages,
            onPrevious = { if (currentPage > 0) currentPage-- },
            onNext = { if (currentPage < totalPages - 1) currentPage++ }
        )

        Label(attrs = Modifier.fontFamily(FONT_FAMILY).toAttrs(), forId = "address") {
            Text("Shipping Address:")
        }
        TextInput(
            value = address,
            attrs = Modifier.fontFamily(FONT_FAMILY).toAttrs() {
                onInput { address = it.value }
                placeholder("Enter your shipping address")
            }
        )

        CheckoutSection(
            firebaseUid = firebaseUid,
            address = address,
            totalPrice = totalPrice,
            cartItem = cartItems.map {
                CartItemDTO(
                    firebaseUid = firebaseUid,
                    quantity = it.quantity,
                    bundleVariantId = it.bundleVariantId,
                    productVariantId = it.productVariantId,
                    price = it.packPrice.toDouble(),
                    productName = if (it.isProduct) it.title else null,
                    bundleName = if (!it.isProduct) it.title else null
                )
            },
            onCheckoutSuccess = { orderId ->
                currentOrderId = orderId
                showCheckoutPage = true
            }
        )
    }
}
@Composable
fun PaginationControls(
    currentPage: Int,
    totalPages: Int,
    onPrevious: () -> Unit,
    onNext: () -> Unit
) {
    val breakpoint = rememberBreakpoint()
    Row(
        horizontalArrangement = Arrangement.Center,
        modifier = Modifier.fillMaxWidth(
            if (breakpoint <= Breakpoint.MD) 80.percent else 100.percent
        )
    ) {
        Button(
            attrs = Modifier
                .borderRadius(r = 15.px)
                .fontFamily(FONT_FAMILY)
                .border(
                    style = LineStyle.Solid,
                    color = Colors.White
                )
                .boxShadow(
                    blurRadius = 10.px,
                    spreadRadius = 0.px,
                    color = Colors.LightGray,
                    offsetX = 0.px,
                    offsetY = 4.px
                )
                .onClick{
                    onPrevious()
                }
                .toAttrs()) {
            Text("Previous")
        }


        P(
            attrs = Modifier
                .margin(leftRight = 10.px)
                .fontFamily(FONT_FAMILY)
                .fontSize(15.px)
                .toAttrs()
        )   {
            Text("${currentPage + 1} of $totalPages")
        }

        Button(
            attrs = Modifier
                .backgroundColor(Colors.PaleTurquoise)
                .borderRadius(r = 15.px)
                .border(
                    style = LineStyle.Solid,
                    color = Colors.White
                )
                .fontFamily(FONT_FAMILY)
                .boxShadow(
                    blurRadius = 10.px,
                    spreadRadius = 0.px,
                    color = Colors.LightGray,
                    offsetX = 0.px,
                    offsetY = 4.px
                )
                .onClick {
                    onNext()
                }
                .toAttrs()
        ) {
            Text("Next")
        }
    }
}


@Composable
fun CartHeader() {
    val breakpoint = rememberBreakpoint()
    Column(
        modifier = Modifier.padding(20.px)
    ) {
        P(
            attrs = Modifier
                .fontSize(24.px)
                .textShadow(
                    blurRadius = 10.px,
                    color = Colors.LightGray,
                    offsetX = 0.px,
                    offsetY = 4.px
                )
                .fontFamily(FONT_FAMILY)
                .fontWeight(FontWeight.Bold)
                .margin(bottom = 20.px)
                .toAttrs()
        ) {
            Text("Shopping Cart")
        }

        Box(
            modifier = Modifier
                .padding(2.px)
                .boxShadow(
                    blurRadius = 10.px,
                    spreadRadius = 0.px,
                    color = Colors.LightGray,
                    offsetX = 0.px,
                    offsetY = 4.px
                )
                .width(
                    when {
                        breakpoint <= Breakpoint.SM -> 300.px // mobile
                        breakpoint <= Breakpoint.MD -> 500.px // tablet
                        else -> 900.px // desktop
                    }
                )
                .margin(bottom = 30.px)
                .backgroundColor(Colors.Gray)
        )
    }
}

private enum class HeaderCart (
    val header: String
) {
    ProductDetails(
        header = "Product Details"
    ),
    Quantity(
        header = "Quantity"
    ),
    Price(
        header = "Price"
    )

}

@Composable
fun CheckoutSection(
    firebaseUid: String,
    address: String,
    totalPrice: Double,
    cartItem: List<CartItemDTO>,
    onCheckoutSuccess: (Int) -> Unit  //  Callback to go to CheckoutPage
) {
    val formattedTotal = totalPrice.asDynamic().toFixed(2) as String
    val coroutineScope = rememberCoroutineScope()

    Column(
        modifier = Modifier
            .fillMaxWidth()
            .padding(20.px)
            .border(style = LineStyle.Solid, color = Colors.White)
            .boxShadow(
                blurRadius = 10.px,
                spreadRadius = 0.px,
                color = Colors.LightGray,
                offsetX = 0.px,
                offsetY = 4.px
            )
            .margin(topBottom = 20.px)
            .backgroundColor(Colors.PaleTurquoise)
            .justifyContent(JustifyContent.SpaceBetween),
        horizontalAlignment = Alignment.End,
        verticalArrangement = Arrangement.Center
    ) {
        P(
            attrs = Modifier
                .fontFamily(FONT_FAMILY)
                .fontWeight(FontWeight.Bold)
                .fontSize(20.px)
                .margin(right = 20.px)
                .toAttrs()
        ) {
            Text("Total: $formattedTotal")
        }

        Button(
            attrs = Modifier
                .backgroundColor(Colors.White)
                .borderRadius(12.px)
                .border(style = LineStyle.None)
                .fontFamily(FONT_FAMILY)
                .padding(leftRight = 10.px, topBottom = 5.px)
                .onClick {
                    coroutineScope.launch {
                        val orderId = CheckoutService.checkout(firebaseUid, address, cartItem)
                        if (orderId != null) {
                            console.log(" Order placed: ID $orderId")
                            onCheckoutSuccess(orderId) //  Trigger navigation
                        } else {
                            console.error(" Failed to checkout")
                        }
                    }
                }
                .toAttrs()
        ) {
            Text("Proceed to Checkout")
        }
    }
}


