package catalystpage.com.database

import dto.UserDTO
import dto.UserProfile
import kotlinx.browser.window
import kotlinx.coroutines.await
import kotlinx.serialization.ExperimentalSerializationApi
import kotlinx.serialization.json.Json
import kotlinx.serialization.json.encodeToDynamic
import org.w3c.fetch.Headers
import org.w3c.fetch.RequestInit

class JsUserFetcher {
    companion object {
        private val json = Json { ignoreUnknownKeys = true }
    }

    suspend fun fetchCurrentUser(): UserDTO {
        val uid = window.localStorage.getItem("firebaseUserUID")

        if (uid.isNullOrBlank()) {
            console.warn(" No Firebase UID in localStorage")
            throw Exception("User not logged in")
        }

        val headers = Headers().apply {
            append("X-Firebase-Uid", uid)
        }

        val response = window.fetch(
            "http://localhost:8081/api/users/me",
            RequestInit(
                method = "GET",
                headers = headers
            )
        ).await()

        if (!response.ok) {
            val errorBody = response.text().await()
            console.error(" Failed to fetch user:", response.status, response.statusText, errorBody)
            throw Exception("Failed to fetch user: ${response.status} ${response.statusText}")
        }

        val responseText = response.text().await()

        if (responseText.isBlank()) {
            console.warn(" User response body is empty")
            throw Exception("User response is empty")
        }

        return json.decodeFromString(responseText)
    }

    suspend fun updateUserDTO(name: String, phone: String) {
        val uid = window.localStorage.getItem("firebaseUserUID")
            ?: throw Exception("User not logged in")

        val headers = Headers().apply {
            append("Content-Type", "application/json")
            append("X-Firebase-Uid", uid)
        }

        // Use the actual UserDTO
        val userDTO = UserDTO(
            firebaseUid = uid,
            name = name,
            phone = phone,
            email = null,
            roleRaw = null
        )

        val updatePayload = Json.encodeToString(userDTO)

        console.log(" Payload being sent to backend:", updatePayload)

        val response = window.fetch(
            "http://localhost:8081/api/users/me",
            RequestInit(
                method = "PUT",
                headers = headers,
                body = updatePayload
            )
        ).await()

        if (!response.ok) {
            val error = response.text().await()
            console.error(" Failed to update user:", error)
            throw Exception("Failed to update user: ${response.status} ${response.statusText}")
        } else {
            console.log(" User updated successfully")
        }
    }

    @OptIn(ExperimentalSerializationApi::class)
    suspend fun syncUserWithBackend(uid: String, email: String) {
        val headers = Headers().apply {
            append("Content-Type", "application/json")
        }

        val userPayload = json.encodeToString(UserDTO(firebaseUid = uid, email = email))

        val response = window.fetch(
            "http://localhost:8081/api/auth/sync",
            RequestInit(
                method = "POST",
                headers = headers,
                body = userPayload
            )
        ).await()

        if (!response.ok) {
            val error = response.text().await()
            console.error(" Failed to sync user with backend:", error)
            throw Exception("Failed to sync user: ${response.status} ${response.statusText}")
        } else {
            console.log(" User synced with backend")
        }
    }


    suspend fun fetchCurrentUserName(): String? {
        return try {
            fetchCurrentUser().name
        } catch (e: Exception) {
            console.error("Error fetching username: ${e.message}")
            null
        }
    }

    suspend fun fetchCurrentUserData(): UserProfile? {
        return try {
            val user = fetchCurrentUser()
            UserProfile(
                username = user.name,
                fullName = user.name,
                email = user.email,
                phone = user.phone
            )
        } catch (e: Exception) {
            console.error("Error fetching user data: ${e.message}")
            null
        }
    }


    suspend fun updateUserData(fullName: String, address: String, birthdate: String) {
        // TODO: Implement this if your API supports PUT or POST update to /users
    }


}