package catalystpage.com.components

import androidx.compose.runtime.*
import catalystpage.com.database.logOutUser
import catalystpage.com.model.DashboardItem
import catalystpage.com.styles.SocialLinkStyle
import catalystpage.com.util.Constants.FONT_FAMILY
import com.varabyte.kobweb.compose.css.Cursor
import com.varabyte.kobweb.compose.css.Overflow
import com.varabyte.kobweb.compose.css.PointerEvents
import com.varabyte.kobweb.compose.foundation.layout.Arrangement
import com.varabyte.kobweb.compose.foundation.layout.Box
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.foundation.layout.Row
import com.varabyte.kobweb.compose.ui.Alignment
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.graphics.Colors
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.compose.ui.toAttrs
import com.varabyte.kobweb.core.rememberPageContext
import com.varabyte.kobweb.silk.components.icons.fa.*
import com.varabyte.kobweb.silk.style.breakpoint.Breakpoint
import com.varabyte.kobweb.silk.style.toModifier
import com.varabyte.kobweb.silk.theme.breakpoint.rememberBreakpoint
import kotlinx.coroutines.launch
import org.jetbrains.compose.web.css.*
import org.jetbrains.compose.web.dom.P
import org.jetbrains.compose.web.dom.Text


@Composable
fun Dashflow(
    selectedItem: DashboardItem,
    onItemSelected: (DashboardItem) -> Unit
) {
    val breakpoint = rememberBreakpoint()
    val isMobile = breakpoint <= Breakpoint.MD

    if (isMobile) {
        //  Bottom bar for mobile
        Row(
            modifier = Modifier
                .fillMaxWidth()
                .height(70.px)
                .position(Position.Fixed)
                .bottom(0.px)
                .left(0.px)
                .zIndex(100)
                .backgroundColor(Colors.White)
                .overflow(Overflow.Hidden)
                .pointerEvents(PointerEvents.Auto),
            horizontalArrangement = Arrangement.SpaceEvenly,
            verticalAlignment = Alignment.CenterVertically
        ) {
            DashboardItem.entries.forEach { item ->
                FaIcon(
                    item = item,
                    isSelected = selectedItem == item,
                    onClick = { onItemSelected(item) }
                )
            }
        }
    } else {
        //  Sidebar for desktop
        Column(
            modifier = Modifier
                .width(150.px)
                .fillMaxHeight()
                .position(Position.Fixed)
                .top(0.px)
                .borderRadius(topRight = 10.px, bottomRight = 10.px)
                .left(0.px)
                .boxShadow(
                    blurRadius = 10.px,
                    spreadRadius = 0.px,
                    color = Colors.Gainsboro,
                    offsetX = 1.px,
                    offsetY = 0.px
                )
                .border(style = LineStyle.None)
                .zIndex(100)
                .backgroundColor(Colors.White)
                .overflow(Overflow.Hidden)
                .pointerEvents(PointerEvents.Auto),
            horizontalAlignment = Alignment.Start,
            verticalArrangement = Arrangement.Center
        ) {
            DashLinks(selectedItem = selectedItem, onItemSelected = onItemSelected)

        }
    }
}


@Composable
private fun DashLinks(
    selectedItem: DashboardItem,
    onItemSelected: (DashboardItem) -> Unit
) {
    Column(
        modifier = Modifier.fillMaxWidth(),
        horizontalAlignment = Alignment.Start,
        verticalArrangement = Arrangement.Center
    ) {
        DashboardItem.entries.forEach { item ->
            FaIcon(
                item = item,
                isSelected = selectedItem == item,
                onClick = { onItemSelected(item) }
            )
        }
        Logout()
    }
}

@Composable
private fun FaIcon(item: DashboardItem, isSelected: Boolean, onClick: () -> Unit) {
    val backgroundColor = if (isSelected) Colors.PaleTurquoise else Colors.White
    val breakpoint = rememberBreakpoint()
    val isMobile = breakpoint <= Breakpoint.MD

    Box(
        modifier = SocialLinkStyle.toModifier()
            .fillMaxWidth()
            .margin(topBottom = 10.px, leftRight = 10.px)
            .backgroundColor(backgroundColor)
            .borderRadius(r = 5.px)
            .padding(topBottom = 10.px, leftRight = 3.px)
            .cursor(Cursor.Pointer)
            .onClick { onClick() },
        contentAlignment = Alignment.TopStart
    ) {
        Row(
            verticalAlignment = Alignment.CenterVertically,
            horizontalArrangement = Arrangement.spacedBy(8.px)
        ) {
            // Icon
            when (item) {
                DashboardItem.Profile -> FaHouseUser(size = IconSize.LG)
                DashboardItem.Shop -> FaShop(size = IconSize.LG)
                DashboardItem.Cart -> FaCartShopping(size = IconSize.LG)
                DashboardItem.Voucher -> FaTicket(size = IconSize.LG)
                DashboardItem.Settings -> FaGear(size = IconSize.LG)
            }

            if (!isMobile) {
                P(
                    attrs = Modifier
                        .margin(top = 2.px)
                        .fontSize(14.px)
                        .fontFamily(FONT_FAMILY)
                        .toAttrs()
                ) {
                    Text(item.title)
                }
            }
        }
    }
}



@Composable
fun Logout() {
    val scope = rememberCoroutineScope()
    val pageContext = rememberPageContext()
    val breakpoint = rememberBreakpoint()
    val isMobile = breakpoint <= Breakpoint.MD

    val backgroundColor = Colors.White

    Box(
        modifier = SocialLinkStyle.toModifier()
            .fillMaxWidth()
            .margin(topBottom = 10.px, leftRight = 10.px)
            .backgroundColor(backgroundColor)
            .borderRadius(r = 5.px)
            .padding(topBottom = 10.px)
            .cursor(Cursor.Pointer)
            .onClick {
                scope.launch {
                    logOutUser()
                    pageContext.router.navigateTo("/signIn")
                }
            },
        contentAlignment = Alignment.TopStart
    ) {
        if (isMobile) {
            Column(
                horizontalAlignment = Alignment.CenterHorizontally,
                verticalArrangement = Arrangement.spacedBy(4.px)
            ) {
                FaDoorOpen(size = IconSize.LG)
                P(
                    attrs = Modifier
                        .fontSize(14.px)
                        .fontFamily(FONT_FAMILY)
                        .toAttrs()
                ) {
                    Text("Logout")
                }
            }
        } else {
            Row(
                verticalAlignment = Alignment.CenterVertically,
                horizontalArrangement = Arrangement.spacedBy(8.px)
            ) {
                FaDoorOpen(size = IconSize.LG)
                P(
                    attrs = Modifier
                        .margin(top = 2.px) // fix vertical alignment
                        .fontSize(14.px)
                        .fontFamily(FONT_FAMILY)
                        .toAttrs()
                ) {
                    Text("Logout")
                }
            }
        }
    }
}
